<template>
  <div class="container">
    <header>
      <button class="ui-btn" v-on:click="sideChangeState">
        <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 24 24" fill="none" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <line x1="3" y1="12" x2="21" y2="12"/>
          <line x1="3" y1="6" x2="21" y2="6"/>
          <line x1="3" y1="18" x2="21" y2="18"/>
        </svg>
      </button>
      <p id="title">{{$route.name}}</p>
      <button class="ui-btn" v-on:click="letterChangeState">
        <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 24 24" fill="none" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <line x1="22" y1="2" x2="11" y2="13"/>
          <polygon points="22 2 15 22 11 13 2 9 22 2"/>
        </svg>
      </button>
    </header>
  </div>
</template>

<script>
export default {
  name: "Header",
  props: [
    "uiData"
  ],
  methods: {
    sideChangeState() {
      this.uiData.backDropActive = !this.uiData.backDropActive;
      this.uiData.sideSection = !this.uiData.sideSection;
    },
    letterChangeState() {
      this.uiData.backDropActive = !this.uiData.backDropActive;
      this.uiData.letterSection = !this.uiData.letterSection;
    }
  }
}
</script>

<style scoped>
  div.container {
    width: 100vw;
    position: fixed;
    top: 0;
  }

  header {
    background: var(--neutral-color-alpha);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 12px;
    height: 50px;
  }

  button svg {
  display: flex;
  justify-self: center;
  height: 20px;
  width: 20px;
  padding: 5px;
  stroke: var(--font-color);
  stroke-width: 1.5px;
  flex: 1;
  transition: stroke 150ms ease-in-out; 
  }

  button:active svg {
    stroke: #ffad1f;
    transition: 0ms;
  }

  p {
    font-size: 22px;
    color: var(--font-color);
    padding: 10px;
    flex: 1;
  }
</style>